<template><section><h1>雷达扫描</h1>
<p><code v-pre>vc-scan-radar</code> 组件用于加载雷达扫描后期处理特效。参考 <a href="https://www.wellyyss.cn/ysCesium/main/app.html">ysCesium|跃焱邵隼</a> 。</p>
<h2>示例</h2>
<h3>加载雷达扫描</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-scan-radar @ready="subReady" :radius="1500" :interval="3000" :color="[0,1.0,0,1]" :position="position"></vc-scan-radar>
        <vc-layer-imagery>
          <vc-provider-imagery-tianditu mapStyle="img_c" token="436ce7e50d27eede2f2929307e6b33c0"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
