  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">position</span>: { <span class="hljs-attr">lng</span>: <span class="hljs-number">117.217124</span>, <span class="hljs-attr">lat</span>: <span class="hljs-number">31.809777</span> }
      }
    },
    <span class="hljs-attr">methods</span>: {
      ready(cesiumInstance) {
        <span class="hljs-keyword">this</span>.cesiumInstance = cesiumInstance
      },
      subReady() {
        <span class="hljs-keyword">const</span> { Cesium, viewer } = <span class="hljs-keyword">this</span>.cesiumInstance
        viewer.scene.globe.depthTestAgainstTerrain = <span class="hljs-literal">true</span>
        viewer.camera.flyTo({
          <span class="hljs-attr">destination</span>: Cesium.Cartesian3.fromDegrees(<span class="hljs-number">117.217124</span>, <span class="hljs-number">31.809777</span>, <span class="hljs-number">3500.0</span>),
          <span class="hljs-attr">orientation</span>: {
            <span class="hljs-attr">heading</span>: Cesium.Math.toRadians(<span class="hljs-number">0</span>), <span class="hljs-comment">// east, default value is 0.0 (north) //东西南北朝向</span>
            <span class="hljs-attr">pitch</span>: Cesium.Math.toRadians(<span class="hljs-number">-90</span>), <span class="hljs-comment">// default value (looking down)  //俯视仰视视觉</span>
