<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>position</td>
<td>Object</td>
<td></td>
<td><code v-pre>required</code> 指定雷达扫描添加的位置。结构：{ lng: number, lat: number, height: number } 或者 Cesium.Cartesian3</td>
</tr>
<tr>
<td>color</td>
<td>Object|String|Array</td>
<td><code v-pre>'white'</code></td>
<td><code v-pre>optional</code> 指定雷达扫描颜色。</td>
</tr>
<tr>
<td>radius</td>
<td>Number</td>
<td><code v-pre>1500</code></td>
<td><code v-pre>optional</code> 指定雷达扫描半径。单位米。</td>
</tr>
<tr>
<td>interval</td>
<td>Number</td>
<td><code v-pre>3000</code></td>
<td><code v-pre>optional</code> 指定雷达扫描完成一个周期重复频率，单位毫秒。</td>
</tr>
</tbody>
</table>
<hr>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例, 以及 PostProcessStage 实例。</td>
</tr>
</tbody>
</table>
</section></template>

<script>
    export default {
      data() {
        return {
          position: { lng: 117.217124, lat: 31.809777 }
        }
      },
      methods: {
        ready(cesiumInstance) {
          this.cesiumInstance = cesiumInstance
        },
        subReady() {
          const { Cesium, viewer } = this.cesiumInstance
          viewer.scene.globe.depthTestAgainstTerrain = true
          viewer.camera.flyTo({
            destination: Cesium.Cartesian3.fromDegrees(117.217124, 31.809777, 3500.0),
            orientation: {
              heading: Cesium.Math.toRadians(0), // east, default value is 0.0 (north) //东西南北朝向
              pitch: Cesium.Math.toRadians(-90), // default value (looking down)  //俯视仰视视觉
              roll: 0.0 // default value
            },
            duration: 3 //3秒到达战场
          })
        }
      }
    }
  </script>